import './styles.css'
import React, {Component} from 'react';
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';

import BadgeComponent from './badge.component'

function linkFormatter(issue, row) {
    return (
        <a target={'_blank'} href={row.self}>{issue}</a>
    );
}

function badgesFormatter(badges, row) {
    return (
        <div className={"btn-group btn-progress"}>
            {badges.filter(b => b.type !== 'all').map((b, i) =>
                <BadgeComponent key={i} badge={b}/>
            )}
        </div>
    );
}


class BoardContentComponent extends Component {
    render() {
        const items = this.props.items
        return (
            <div className={'jira-content'}>
                <div className={'jira-table'}>
                    <BootstrapTable data={items} striped hover version='4'>
                        <TableHeaderColumn dataField='key' isKey width='15%'
                                           dataFormat={linkFormatter}>Key</TableHeaderColumn>
                        <TableHeaderColumn dataField='summary' width='55%'
                                           dataFormat={linkFormatter}>Summary</TableHeaderColumn>
                        <TableHeaderColumn dataField='badges' dataFormat={badgesFormatter}
                                           width='20%'>Issues</TableHeaderColumn>
                    </BootstrapTable>
                </div>
            </div>
        );
    }
}

export default BoardContentComponent;