import {Subject} from 'rxjs';
import socketsService from "../services/widget-sockets-register.service";

class DataLoadBaseJob {

    sources;
    type;

    constructor(type, options) {
        this.type = type;
        this.sources = new Subject();
        socketsService.connect(this.type, options);
    }

    start() {
        socketsService
            .listen(this.type,
                (data) => {
                    this.sources.next(data);
                },
                error => {
                    this.sources.error(error);
                });

        return this.sources.asObservable();
    }

    dispose() {
        socketsService.disconnect(this.type);
    }

}

export default DataLoadBaseJob;