import DataLoadBaseJob from "./data-load-base.job";

class GetJiraBoardIssuesJob extends DataLoadBaseJob {

  constructor(type, boardId, jql, maxResults) {

    var options = {
      service: "get-jira-board-issues-job",
      data: {
        cronTime: "* */5 * * * *",
        boardId,
        jql,
        maxResults
      }
    };
    super(type, options);
  }

}

export default GetJiraBoardIssuesJob;