import GetJenkinsLastBuildsJob from './get-jenkins-last-builds.job';
import GetJenkinsLastBuildsJobFake from './get-jenkins-last-builds.job.fake';
import jobNames from './job-names.enum'
import configService from './../services/config.service'

class JobsFactory {
  create(jobName, id, options) {
    if (configService.env.useFakeData) {
      return this.createFakes(jobName);
    }
    console.log(jobName);
    switch (jobName) {
      case jobNames.getJenkinsLastBuildsJob:
        console.log("options ", options);
        return new GetJenkinsLastBuildsJob(id, options.jenkinsName, options.jobNames);
        break;
      default:
        throw new Error(`${jobName} - is not registered.`);
    }
  }

  createFakes(jobName) {
    switch (jobName) {
      case jobNames.getJenkinsLastBuildsJob:
        return new GetJenkinsLastBuildsJobFake();
        break;
      default:
        throw new Error(`${jobName} - is not registered.`);
    }
  }
}

export default new JobsFactory()