import './styles.css'
import React, {Component} from 'react';
import * as shortid from 'shortid';
import BuildComponent from "./build.component";
import jobsFactory from "../../data-load-jobs/data-loader-jobs.factory";
import jobNames from "../../data-load-jobs/job-names.enum";
import PanelLoaderComponent from "../../core/components/panel-loader";


class JenkinsBuildsListComponent extends Component {
  dataLoaderJob;

  constructor(props) {
    console.log("JenkinsBuildsListComponent", props);
    super(props);
    this.state = {
      builds: [],
      isLoading: true
    };

  }

  componentDidMount() {
    console.log(this.props);
    this.dataLoaderJob = jobsFactory.create(
      jobNames.getJenkinsLastBuildsJob,
      this.props.id,
      {
        jenkinsName: this.props.jenkinsName,
        jobNames: this.props.jobNames
      });
    this.dataLoaderJobSubscription = this.dataLoaderJob
      .start()
      .subscribe((data) => {
        console.log("dataLoaderJobSubscription ", data);
        this.state.builds = data.map(d => this.state.builds.find(b => b.displayName == d.displayName) || d);
        console.log(" this.state.builds ", this.state.builds);

        this.setState({
          builds: this.state.builds,
          isLoading: this.state.builds.length === 0
        });
        console.log(" setState ", this.setState);
      }, () => {
        this.setState({
          ...this.state,
          errorMessage: "Loading error..."
        });
      });
  }

  componentWillUnmount() {
    this.dataLoaderJobSubscription.unsubscribe();
    this.dataLoaderJob.dispose();
  }

  renderBuilds(builds) {
    return (builds.map(build =>
      <BuildComponent key={shortid.generate()} data={build}/>
    ))
  }

  render() {
    return <PanelLoaderComponent panel={this.renderBuilds(this.state.builds)}
                                 isLoading={this.state.isLoading}
                                 errorMessage={this.state.errorMessage}/>;
  }
}

export default JenkinsBuildsListComponent