import {Subject} from 'rxjs';

class DataLoadBaseJobFake {
  sources;
  data;

  constructor(data) {
    this.data = data;
    this.sources = new Subject();
  }

  start() {
    let observable = this.sources.asObservable();
    setTimeout(() => {
      this.sources.next(this.data);
    }, 1000);


    return observable;
  }

  dispose() {
  }

}

export default DataLoadBaseJobFake;