import React, {Component} from 'react';

import JiraCreatedResolvedIssuesChartComponent from "../../components/jira-created-resolved-issues-chart"

class JiraBugsChartComponent extends Component {

    render() {
        return <JiraCreatedResolvedIssuesChartComponent jobId={'jira-bugs-chart'} reportTitle={'Bugs'} conditions={['project=DDM', 'issuetype in (Bug)']}/>
    }
}

export default JiraBugsChartComponent