import * as io from 'socket.io-client';
import configService from "./config.service";


class WidgetSocketsRegisterService {
    connections = {};

    connect(id, options) {
        this.connections[id] = io.connect(configService.env.apiUrl);
        this.connections[id].emit("register-widget", {id, ...options});
    }

    disconnect(id) {
        this.connections[id].emit('unregister-widget', {id});
    }

    listen(id, resolve, reject = undefined) {
        this.connections[id].on('on-data-updated', (response) => {
            if (response.error && reject) {
                reject(response.error);
                return;
            }

            resolve(response.data)
        })
    }
}


export default new WidgetSocketsRegisterService();